import {useRef} from "react";
import axios from "axios";
import {useNavigate} from "react-router-dom";


function ManageSignUp() {
    const idRef = useRef(null);
    const pwdRef = useRef(null);
    const navigate = useNavigate();
    function signUpOnClick() {

        const idChkVal = idRef.current.value;
        const pwdChkVal = pwdRef.current.value;

        // -->   , 8  16  .
        const pwdValiForm = /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$/;

        if(idChkVal.search(/\W|\s/g) > -1 ) {
            idRef.current.focus();
            alert("   .");
            return;
        } else if (!idChkVal) {
            alert(" ");
            return;
        } else if(!pwdChkVal) {
            alert(" ");
            return;
        } else if (!pwdValiForm.test(pwdChkVal)) {
            alert("     ,\n8 , 16  ");
            pwdRef.current.focus();
            return;
        }
        // 
        idDupliChk(idChkVal);

        axios.post(`/member/v1/savemanage`, {
            memberId: idChkVal,
            pwd: pwdChkVal
        },
        {
            headers : {
                AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
            }
        }).then(function (res) {
            if(res.status === 403) {
                navigate('/member/signin');
            }
            console.log(res)
            alert("   .")
        }).catch(function (err){
            console.log(err)
            alert("    .   ")
        })
    } //  

    function idDupliChk(idChkVal) {
        axios({
            url: `/member/v1/chkdupliId?memberId=${idChkVal}`,
            method: 'get'
        }).then(function (res) {
            if(res.status === 403) {
                navigate('/member/signin');
            }
            if(res.data === 1) {
                alert("  ")
            }
        }).catch(function (err) {
            console.log(err)
            alert("    .");
        });
    }
    return (
    <div>
        <div className="d-flex  justify-content-center">
            <h1>  </h1>
        </div>
        <hr/>

        <div className="d-flex  justify-content-center" style={{paddingBottom: 7}}>
            <input type="text" placeholder=" " ref={idRef}/>
        </div>
        <div className="d-flex  justify-content-center" style={{paddingBottom: 7}}>
            <input type="password" placeholder=" " ref={pwdRef}/>
        </div>

        <div className="d-flex  justify-content-center">
            <button type="button" className="btn btn-warning"
                onClick={ () => {
                    signUpOnClick()
                }}
                style={{backgroundColor: "#217Af0",
                        color: "white",
                        width: 178.5}}>  </button>
        </div>
    </div>
    )
}

export default ManageSignUp;