import style from "../../css/signUpStyle.css";
import {useEffect, useState} from "react";
import Post from "components/Post";
import {useRef} from "react";
import axios from "axios";
import {useLocation, useNavigate, useParams} from "react-router-dom";


function SignUp() {
    const emailData = useLocation();
    const socialEmail = emailData.search.substring(7);


    const [dupli, setDupli] = useState(0);
    const [disabled, setDisabled] = useState(false);
    const navigate = useNavigate();

    const nameRef =useRef(null);
    const idRef = useRef(null);
    const pwdRef = useRef(null);
    const pwdConfirmRef = useRef(null);
    const phoneNumRef = useRef(null);
    const phoneSelect = useRef(null);
    const searchAddr = useRef(null);
    const addrDetailRef = useRef(null);

    const dupliIdChk = async () => {


        // id   
        const idChkVal = idRef.current.value;

        if(idChkVal.search(/\W|\s/g) > -1 ) {
            idRef.current.focus();
            alert("   .");
            return;
        } else if (idChkVal == "") {
            alert(" .");
            idRef.current.focus();
            return;
        } else if (idChkVal.length < 4 || idChkVal.length > 12) {
            alert(' 4 12  ');
            return;
        }

        const uri = `/member/v1/chkdupliId?memberId=${idChkVal}`;
        const encoded = encodeURI(uri);
        const json = await (
            await fetch(encoded)
        ).json();

        //   
        setDupli(json);

        if (json == 1) {
            alert("  .")
            idRef.current.focus();
            return;
        } else if (json == 0) {
            const useCfn = window.confirm("    ?")
                if(useCfn === true) {
                    setDisabled(true);
                    console.log(disabled);
                }
        } else {
            alert("   .    .")
        }
    }

    //     
    function onSubmit(e) {
        const nameValiForm = /^[-]+$/;
        // -->   , 8  16  .
        const pwdValiForm = /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$/;
        const phoneValiForm = /^01(?:0|1|[6-9])-(?:\d{3}|\d{4})-\d{4}$/;

        e.preventDefault();

        const idVal = idRef.current.value;
        const nameVal = nameRef.current.value;
        const pwdVal = pwdRef.current.value;
        const pwdConfirmVal = pwdConfirmRef.current.value;
        const phoneVal = phoneNumRef.current.value;
        const phoneSelectVal = phoneSelect.current.value;
        const searchAddrVal = searchAddr.current.value;
        const addrDetail = addrDetailRef.current.value;

        if (nameVal == "") {
            alert(" ");
            nameRef.current.focus();
                return;
        } else if (!nameValiForm.test(nameVal)) {
            alert("  ");
            nameRef.current.focus();
                return;
        } else if (pwdVal !== pwdConfirmVal) {
            alert("    .");
            pwdRef.current.focus();
            return;
        } else if (!pwdValiForm.test(pwdVal)) {
            alert("     ,\n8 , 16  ");
            pwdRef.current.focus();
            return;
        } else if(phoneSelectVal === "") {
            alert(" ");
            return;
        } else if (!phoneValiForm.test(phoneVal)) {
            alert("     .");
            return;
        } else if (searchAddrVal  === "") {
            alert(" .");
            return;
        } else if (addrDetail === "") {
            alert("  ");
            return;
        } else if (disabled === false) {
            alert("  .");
            return;
        }
        axios.post("/member/v1/save", {
            memberId: idVal,
            name: nameVal,
            pwd: pwdVal,
            email: socialEmail,
            telNo: phoneVal,
            address: searchAddrVal,
            detailAddr: addrDetail
        })
            .then(function (response) {
                console.log('res : ' + response.status);
                window.alert("welcome trade!!\n  !");
                navigate(`/`)
            }).catch(function (error) {
                //  
                console.log('error message : '+ error);
                window.alert("     .");
        }); // axios
    } // onSubmit end

    //-------------------------------- api----------------------
    const [enroll_company, setEnroll_company] = useState({address:'', });
    const [popup, setPopup] = useState(false);
    const handleInput = (e) => {
        setEnroll_company({
            ...enroll_company,
            [e.target.name]:e.target.value,
        })
    }
    const handleComplete = (data) => {
        setPopup(!popup);
    }
    //-------------------------------- api----------------------
    return (
        <div>
            <div id="con">
                <div id="login">
                    <div id="login_form">
                            <h3 className="login" style={{letterSpacing: -1}}>Welcome Trade :)</h3>
                            <br/>
                        <div className="d-flex justify-content-start">
                            <label >
                                <div style={{display: "flex", padding: 5}}>
                                    <p style={{textAlign: "left", fontSize: 12, color:"#666", paddingRight: 5}}>user id</p>
                                </div>
                            </label>
                            <div>
                                <button className="btn"
                                        style={{fontSize: 12,backgroundColor: "#217Af0", width: 50, height:10, paddingBottom: 21, color: "white"}}
                                        onClick={dupliIdChk}>  </button>
                            </div>
                        </div>
                        <input type="text"
                               placeholder=""
                               className="size"
                               style={{marginBottom: 15}}
                               ref={idRef}
                               disabled={disabled === true} />
                        <br />
                            <label>
                                <p style={{textAlign: "left", fontSize: 12, color:"#666"}}>user name</p>
                                <input type="text" placeholder="" className="size" style={{marginBottom: 15}} ref={nameRef}/>
                            </label>
                            <br />

                            <label>
                                <p style={{textAlign: "left", fontSize: 12, color:"#666"}}>Password </p>
                                <input type="password" placeholder=" , 8  16 " className="size" ref={pwdRef}/>
                            </label>
                            <br />
                            <label>
                                <p style={{textAlign: "left", fontSize: 12, color:"#666"}}></p>
                                <input type="password" placeholder=" " className="size" ref={pwdConfirmRef}/>
                            </label>
                            <br />
                            <label >
                                <p style={{textAlign: "left", fontSize: 12, color:"#666", paddingTop: 15}}>Mobile Phone</p>
                                <select className="size num1" ref={phoneSelect} >
                                    <option defaultValue="1" ></option>
                                    <option defaultValue="2" >SKT</option>
                                    <option defaultValue="3" >KT</option>
                                    <option defaultValue="4" >LG</option>
                                </select>
                                <input placeholder="00*-000*-0000" className="size num2" ref={phoneNumRef} maxLength={13}/>
                            </label>

                            <br/>
                            <label style={{paddingBottom: 20}}>
                                <br />
                                <p style={{textAlign: "left", fontSize: 12, color:"#666", paddingTop: -20}}>E-mail</p>
                                <input type="text" defaultValue={socialEmail} className="size" style={{paddingTop: -20}} disabled={true}/>
                            </label>
                            <br />

                        <div className="d-flex justify-content-start" style={{height:60}}>
                            <label>
                                {popup && <Post company={enroll_company} setcompany={setEnroll_company}></Post>}
                                <br />
                                <div >
                                    <p style={{ fontSize: 12, color:"#666", paddingRight: 8, paddingTop:5}} align="center"></p>
                                </div>
                            </label>
                            <div style={{paddingTop: 20}}>
                                <button type="button"
                                        className="btn"
                                        style={{fontSize:11,fontWeight: "bold", color: "white", backgroundColor: "#217Af0", width: 70, height: 30}}
                                        onClick={() => {
                                            handleComplete()}}
                                        onChange={handleInput}>  </button>
                            </div>
                        </div>
                            <div>
                                <input type="text"
                                       placeholder=" "
                                       className="size"
                                       defaultValue={enroll_company.address}
                                       ref={searchAddr}
                                />
                            </div>
                            <br />
                            <div style={{marginTop: -8, paddingBottom: -100}} >
                                <input type="text" placeholder="" className="size" ref={addrDetailRef}/>
                            </div>
                            <br />
                            <br />
                        <form onSubmit={onSubmit}>
                            <p style={{marginTop: -15}}>
                                <input type="submit" Value="!" className="btn" style={{backgroundColor: "#217Af0", color: "white"}}/>
                            </p>
                        </form>

                        <hr/>
                        <p className="find">
                            <span><a href="/member/signin">  </a></span>
                        </p>
                    </div>
                </div>
            </div>

        </div>
                )}
        export default SignUp;