import React, {useState, useEffect, useRef} from "react";
import {Link, useParams} from "react-router-dom";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import axios from "axios";
import {v4} from 'uuid';


function ItemDetail() {

    const [data, setData] = React.useState([]); // DTO Data
    const [images, setImages] = React.useState([]); // Image Data(Binary)
    const {id} = useParams();
    const navigate = useNavigate();

    //use Effect
    /*
         Data
     */
    let replyList = [];
    const [refresh, setRefresh] = React.useState(""); //    refresh  state
    useEffect(() => {
        const getData = async () => {
            //  ->  : data
            const item = await axios.get('/item/v1/detailPost', {
                params:{
                    id: id
                }
            });
            setData(item.data);
            console.log(item.data);
        }
        getData();
        // console.log("length? " + data.replys);
        //  to Server
        // const dataImageList = item.data.images; // Image List
        // console.log('  : '+dataImageList);
        //
        // const getImage = async () => {
        //     const fromServerImage = await axios.get('/image/v1/showImageList');
        //
        //     // setImages(fromServerImage.data)
        // }//getImage
        // getImage();
        // console.log(images.data);
    }, []);//use Effect

    // Refresh
    const reSearch = async () => {
        const getRes = async () => {
            //  ->  : data
            const item = await axios.get('/item/v1/detailPost', {
                params:{
                    id: id
                }
            });
            setData(item.data);
            console.log(item.data);
        }
        getRes();
    }//research


    /*
          .
     */
    //  
    const onClickDelete = () => {
        const confirm = window.confirm("  ?");
        if (confirm === true) {
            const delData = async () => {
                const res = await axios.delete('/item/v1/deletePostOne?id='+id*1, {
                    headers: {
                        AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
                    }
                }).then(function (response) {
                    if(response.status === 200) {
                        window.alert("  .");
                        navigate('/item/itemBoard');
                    }
                }).catch(function (error) {
                    if(error.response.status === 403) {
                        navigate("/member/signin");
                    }
                    window.alert("   . " + error);
                });
            }// delData
            delData();
        }//if
    }// delete

    /*
     
    */
    const writerId = localStorage.getItem("memberId") //  ID
    let replyId = 0; //  ID
    const answerRef = useRef(null);

    //  post
    const onClickReply = () => {
        // 
        if(localStorage.getItem("memberId") === null) {
            const rCon = window.confirm("     !");
            if(rCon === true) {
                navigate("/member/signin");
                return;
            }else {
                return;
            }
        }

        const replyInputVal = answerRef.current.value; //  .
            axios.post('/item/v1/reply/auth/save', {
                    boardId: id,
                    writer: writerId,
                    content: replyInputVal
            }, {
                headers: {
                    "Content-Type": `application/json`,
                    AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
                }}).then(function (response){
                console.log('  ');
                answerRef.current.value = "";
                // navigate('/itemDetail/'+id);
                reSearch();
            }).catch(function (error){
                if(error.response.status === 403) {
                    navigate("/member/signin");
                }
                console.log('  ' + error);
                window.alert('   .');
            });
    }//on click

    // delete
    const onClickReplyDelete = (e) => {
        //  disabled
        let replyId = e.target.getAttribute('data-id'); //  Long
        let replyWriter = e.target.getAttribute('data-writer'); //  

        //  
        if(replyWriter === localStorage.getItem("memberId")
            || localStorage.getItem("memberId") === "admin" || localStorage.getItem("memberId") === "manager") {
            // Delete
            const data = {id: replyId*1};
            const cfm = window.confirm('   ?');
            if(cfm === true){
                axios.delete('/item/v1/reply/auth/delete?id='+ replyId*1, {
                    headers: {
                        AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
                    },
                }).then(function (res) {
                    console.log('  ');
                    reSearch();
                }).catch(function (error) {
                    if(error.response.status === 403) {
                        navigate("/member/signin");
                    }
                    console.log('    : '+error);
                    window.alert('   ');
                })
            }
        }else if (localStorage.getItem("memberId") === null || replyWriter !== localStorage.getItem("memberId")) {
                window.alert("    .");
                return;
        }
    } //del

    //  update
    const [selectIndex, setSelectIndex] = useState(0);
    const [modiTextArea, setModiTextArea] = useState(false);
    const modiAnswerRef = useRef(null); //  texe area
    let writeDate = ""; //  list date format

    function modiInputShow (e) {
        replyId = e.target.getAttribute("data-id");
        setSelectIndex(replyId);

        if (!modiTextArea ) {
            setModiTextArea(true);
        } else {
            setModiTextArea(false);
        }
    }

    const answerClickUpdate = (e) => {
        const modiAnswerVal = modiAnswerRef.current.value; // textarea
        replyId = e.target.getAttribute("data-id"); //  
        const replyWriter = e.target.getAttribute("data-writer"); //   id

        if(replyWriter !== localStorage.getItem("memberId")) {
            window.alert("    .")
            return;
        }


        axios.put(`/item/v1/reply/auth/update`, {
            id: replyId,
            writer: localStorage.getItem("memberId"),
            content: modiAnswerVal,
            boardId: id
        },{
            headers: {
                AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
            }
            }).then(function (res){
                window.alert("  .");
                setModiTextArea(false);
                reSearch();
            }).catch(function (err) {
                if(err.response.status === 403) {
                    navigate("/member/signin");
                }
                window.alert("    .");
            });
    }

    //  
    const clickApply = () => {
        //  
        if(data.type === '') {
            window.alert('   .');
            return;
        }
        //  
        if(localStorage.getItem("memberId") == null) {
            const conf = window.confirm("  !");
            if(conf === true) {
                navigate("/member/signin");
                return;
            }else {
                return;
            }
        }

        navigate('/applyWrite/' + data.id + '/' + data.writer);
    }
    //   :  ONLY
    const clickFin = () => {
        const confirm = window.confirm("   '' ?");
        if(!confirm) {
            return;
        }

        if(localStorage.getItem("memberId") !== data.writer) {
            window.alert("   .")
            return;
        }

        axios.put('/item/v1/updateStatus', {
            id: id,
            type: ""
        }, {
            headers: {
                AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
            }
        }).then(function (res){
            window.alert("  .");
            window.location.reload();
        }).catch(function (err) {
            if(err.response.status === 403) {
                navigate('/member/signin');
            }
            window.alert("    .");
        })


    }


    return (
        // padding: 5px 1px 2px 3px
        <div>
            <div align="center" style={{ padding : 50 , paddingRight: 60, paddingLeft: 60, paddingTop: 100, paddingBottom: 60}}>
                <h1> : {data.title}</h1>
                <div align="left">
                    <table className="table table-striped table-bordered table-hover">
                        <tbody>
                        <tr>
                            <th scope="row"> </th>
                            <td>{data.id}</td>
                            <th scope="row"></th>
                            <td colSpan='3'>{data.view}</td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td >{data.writer}</td>
                            <th scope='row'> </th>
                            {
                                data.type === ''
                                    ?
                                    <td style={{color:"green"}}>
                                        {data.type}
                                    </td>
                                    :<td style={{color:"red"}}>
                                        {data.type}
                                    </td>
                            }
                            <th scope="row"></th>
                            <td>{data.createdDate}</td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="5">
                                {data.title}
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="6" align="center">
                                {
                                    data.type === ''
                                    ?(localStorage.getItem("memberId") === data.writer
                                        ? <button className="btn btn-warning"
                                                    style={{backgroundColor: "#217Af0", width: 150, color: "white"}}
                                                    onClick={clickFin}>
                                                 
                                          </button>
                                        :<button className="btn btn-warning"
                                                style={{backgroundColor: "#217Af0", width: 150, color: "white"}}
                                                onClick={clickApply}>
                                            
                                        </button>
                                        ):(
                                            localStorage.getItem("memberId") === data.writer
                                            ?<button className="btn btn-warning"
                                                     style={{backgroundColor: "#217Af0", width: 300, color: "white"}}
                                                     onClick={clickApply}
                                                     disabled={true}>
                                                      .
                                                </button>
                                                :
                                                <button className="btn btn-warning"
                                                        style={{backgroundColor: "#217Af0", width: 300, color: "white"}}
                                                        onClick={clickApply}
                                                        disabled={true}>
                                                      .
                                                </button>
                                        )
                                }
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="6" height="300">
                                {data.content}
                            </td>
                        </tr>
                        </tbody>
                    </table>
                {/*                         */}
                {/*<img  style={{height:'30%',width:'30%'}}*/}
                {/*        src={'/Users/seokjunKang/intellij-gradle/day-file/30c746db-55f4-4f12-a993-72df17b8b78d.png'}/>*/}
                </div>

                {/* ,   */}
                {localStorage.getItem("memberId") === data.writer
                    || localStorage.getItem("memberId") === 'admin' || localStorage.getItem("memberId") === 'manager'
                    ?
                    <div align="right">
                    <button className="btn btn-warning"
                    style={{backgroundColor: "#217Af0", width: 100, color: "white"}}>
                    <Link
                    to={`/itemBoardUpdate/${data.id}/${data.title}/${data.writer}/${data.content}/${data.createdDate}`}
                    style={{color: "white"}}>
                     
                    </Link>
                    </button>
                    <button onClick={onClickDelete} className="btn btn-warning"
                    style={{backgroundColor: "#217Af0", width: 100, color: "white"}}>
                     
                    </button>
                    </div> : <div></div>
                }

                {/*  */}
                    <div align="center" style={{ padding : 75}}>
                    <hr />
                    {/* form*/}
                    <table border={0}>
                        <tbody>
                        <tr height="50">
                            <td width="10%" className="col1" align="center"> </td>
                            <td width="90%" className="col2">
                                <div style={{float:"left"}}>
                                    <textarea  style={{width: 800, height: 100}} ref={answerRef}></textarea>
                                </div>
                                {/*style="padding:;">  */}
                                <div style={{float:"left", paddingTop: 34, paddingRight: 12, paddingLeft:34, paddingBottom: 12}}>
                                    <button className="btn btn-warning"
                                            style={{backgroundColor: "#217Af0", width: 100, color: "white"}}
                                            onClick={onClickReply}>   </button>
                                </div>
                            </td>
                        </tr>
                        </tbody>
                    </table>

                    {/* list*/}
                    <br/>
                    <h3> </h3>
                    {data.replys <= 0 ? <h4>   !</h4>
                        :
                        <table border={1} className="table table-striped table-bordered ">
                            <tbody>
                            {data.replys && data.replys.map(answer => (
                                <tr key={v4()}>
                                    <td>
                                        <div border={1}>
                                            <div style={{backgroundColor: "#EBEBEB"}}>
                                                <div style={{
                                                    padding: 10,
                                                    fontWeight: "bold"
                                                }}> {answer.writer} {writeDate = answer.createdDate.substring(0, 10)}</div>
                                            </div>
                                        </div>
                                        <div>
                                            {answer.content}
                                            <div>

                                            </div>
                                            {modiTextArea && selectIndex == answer.id ? (
                                                <div style={{float: "left"}}>
                                                    <textarea style={{width: 800, height: 100}}
                                                              placeholder=" " ref={modiAnswerRef}/>

                                                    <div align="right">
                                                        <button className="btn btn-warning"
                                                                style={{
                                                                    backgroundColor: "#217Af0",
                                                                    width: 45,
                                                                    height: 25,
                                                                    color: "white",
                                                                    fontSize: 10
                                                                }}
                                                                data-id={answer.id}
                                                                data-writer={answer.writer}
                                                                onClick={answerClickUpdate}
                                                        > 
                                                        </button>
                                                        <button className="btn btn-warning"
                                                                style={{
                                                                    backgroundColor: "#217Af0",
                                                                    width: 70,
                                                                    height: 25,
                                                                    color: "white",
                                                                    fontSize: 10
                                                                }}
                                                                onClick={modiInputShow}> 
                                                        </button>
                                                    </div>
                                                </div>
                                            ) : (
                                                <div align="right">
                                                    <button className="btn btn-warning"
                                                            style={{
                                                                backgroundColor: "#217Af0",
                                                                width: 45,
                                                                height: 25,
                                                                color: "white",
                                                                fontSize: 10
                                                            }}
                                                            data-id={answer.id}
                                                            data-writer={answer.writer}
                                                            onClick={(e) => {
                                                                onClickReplyDelete(e)
                                                            }}>
                                                    </button>

                                                    <button className="btn btn-warning"
                                                            style={{
                                                                backgroundColor: "#217Af0",
                                                                width: 45,
                                                                height: 25,
                                                                color: "white",
                                                                fontSize: 10
                                                            }}
                                                            data-id={answer.id}
                                                            onClick={modiInputShow}>
                                                    </button>
                                                </div>
                                            )}
                                        </div>
                                    </td>
                                </tr>
                            ))}
                            </tbody>
                        </table>
                    }
                </div>
            </div>
        </div>
    ) //return
}//func

export default ItemDetail;