import React, {useState, useEffect, useRef} from 'react';
import QnaBoardTable from "../../components/QnaBoardTable";
import 'bootstrap/dist/css/bootstrap.css';
import {Link, useNavigate} from "react-router-dom";


import Pagination from "react-js-pagination";
import {authTokenCheck} from "../../components/AuthTokenCheck";

function QnaBoard() {
    const navi = useNavigate();
    const [qnaList, setQnaList] = useState([]);
    const [totalElements, setTotalElements] = useState(0);
    const [page, setPage] = useState(1);
    const [isCheckingBox, setIsCheckingBox] = useState(false)

    useEffect(() => {
        const chkVal = authTokenCheck(localStorage.getItem("auth_token"));
        if(chkVal == null) {
            navi("/member/signin");
        }
    }, []);

    const checkingCheckedBox = (e) => {
        if (e.target.checked) {
            setIsCheckingBox(true);
        } else if(!e.target.checked) {
            setIsCheckingBox(false);
        }
    }

    const handlePageChange = (page) => {
        setPage(page);
    };

    useEffect(() => {
        if(isCheckingBox) {
            const getData = async () => {
                const res = await fetch(`/qna/v1/qnabywriter?page=${page}&writer=${localStorage.getItem("memberId")}`, {
                    headers: {
                        AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
                    }
                });
                if(res.status === 403) {
                    // 403  -->  ,         .
                    navi("/member/signin");
                }
                const data = await res.json();
                console.log("   data", data)
                setQnaList(data);
                setTotalElements(data.totalElements);
            }
            getData();
        } else {
            const getData = async () => {
                const res = await fetch(`/qna/v1/qnabylist?page=${page}`, {
                    headers: {
                        AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
                    }
                })
                if(res.status === 403) {
                    // 403  -->  ,         .
                    navi("/member/signin");
                }
                const data = await res.json();
                console.log("   data", data)
                setQnaList(data);
                setTotalElements(data.totalElements);
            }
            getData();
        }
    }, [isCheckingBox, page])

    return (

        <div>
            <div align="center" style={{padding : 100}}>
                <div>
                    <h1> </h1>
                </div>

                <QnaBoardTable data={qnaList} />
                <div align="right">
                    <button className="btn btn-warning" style={{fontWeight: "bold", color: "white", backgroundColor: "#217Af0", width: 100}}>
                        <Link to={"/qnawrite"} style={{color: "white"}}>
                        
                        </Link>
                    </button>
                </div>
                <div align="center" >
                    <input type="checkbox" style={{margin: 3, zoom: 1.5}} onClick={checkingCheckedBox}  />
                    <label style={{margin: 3 }}>       .</label>
                </div>
                <Pagination
                    activePage={page} //  
                    itemsCountPerPage={10} //      
                    totalItemsCount={totalElements} //   
                    pageRangeDisplayed={5} // paginator  
                    prevPageText={""} // ""  
                    nextPageText={""} // ""  
                    onChange={handlePageChange} //    
                />
            </div>
        </div>
    )
}

export default QnaBoard;
