import {useRef} from "react";
import axios from "axios";
import {useNavigate} from "react-router-dom";

function MemberSecession() {
    const idRef = useRef(null);
    const navigate = useNavigate();

    const logout = () => {
        axios.post('/logout')
            .then(function res(response){})
    }

    function deleteMember() {
        let memberId = idRef.current.value;
        console.log(memberId)
        if(memberId.search(/\W|\s/g) > -1) {
            idRef.current.focus();
            alert("   .");
        } else if(!memberId) {
            idRef.current.focus();
            alert(" ");
        } else if(memberId.length < 4 || memberId.length > 12) {
            idRef.current.focus();
            alert(' 4 12  ');
        } else if(localStorage.getItem("memberId") !== memberId) {
            alert('     .');
        } else {
            let deleteConfirm = window.confirm('  ?');
            if(deleteConfirm === true) {
                const url = "/member/v1/memberdelete"
                axios.delete(url, {
                headers : {
                    AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
                },
                data: {
                    memberId: memberId
                },
                withCredentials: true,
                }).then(function (res) {
                    console.log(res);
                    if(res.status === 403) {
                        navigate('/member/signin');
                    }
                    if(res.data === 1) {
                        localStorage.removeItem("auth_token");
                        localStorage.removeItem("memberId");
                        localStorage.removeItem("userRole");
                        window.location.assign("/");
                    } else {
                        alert("  .     ")
                    }
                }).catch(function (err) {
                    console.log(err)
                    alert("    .")
                });
            }
        } // else
    } // function


    return (
        <div>
            <div className="d-flex justify-content-center" style={{paddingBottom: 10}}>
                <input type="text"
                       ref={idRef}
                       maxLength={12}
                       style={{width:300}}
                       placeholder=" "
                />
            </div>
            <div className="d-flex justify-content-center">
                <div>
                    <button type="button"
                            className="btn"
                            style={{backgroundColor: "#217Af0"}}
                            onClick={ () => {
                                deleteMember()
                                logout()
                            } }>  </button>
                </div>
            </div>
        </div>
    )
}

export default MemberSecession;