import {Link, useNavigate, useParams} from "react-router-dom";
import React, {useRef} from "react";
import axios from "axios";

/************
 * @info :    ( )
 * @name : NoteWrite
 * @date : 2023/01/03 7:34 PM
 * @author : SeokJun Kang(swings134@gmail.com)
 * @version : 1.0.0
 * @Description :
 *
 * - Param : Note.JS  Param : receiveId .
 * -  .
 *
 ************/
function NoteWrite() {

    const navigate = useNavigate();
    const {receiveId} = useParams(); //  ID
    const senderId = localStorage.getItem("memberId");

    //ref
    const contentRef = useRef(null);
    const titleRef = useRef(null);

    // 
    //  
    function onClickReject() {
        navigate(-1);
    }

    //  
    function onClickSubmit() {
        // val
        const contentVal = contentRef.current.value;
        const titleVal = titleRef.current.value;

        if(titleVal === null || titleVal === ' ') {
            alert(" .");
            return;
        }else if(contentVal === null || contentVal ===' ') {
            alert(" .");
            return;
        }

        //  
        let confirm1 = window.confirm(" ?");
        if(confirm1 === false) {
            return;
        }
        //   &   denied
        if(receiveId === senderId) {
            window.alert("    !");
            return;
        }

            // 
            axios.post('/note/v1/auth/sendnote', {
                receiveMemberId: receiveId, //  
                senderMemberId: senderId, //  
                title: titleVal,
                content: contentVal
            }, {
                headers: {
                    AUTHORIZATION:"Bearer "+localStorage.getItem("auth_token")
                }
            }).then(function (res){
                if(res.status === 200){
                    alert("  .");
                    navigate(-2);
                }
            }).catch(function (err){
                if(err.response.status === 403){
                    navigate("/member/signin");
                }
                alert("    .\n    .");
            })

    }

    return (
        <div>
            <div align="center" style={{padding: 100, paddingBottom: 20}}>
                <h3> </h3>
                <table className="table table-striped table-bordered table-hover" style={{width: 1000}}>
                    <tbody>
                    <tr>
                        <th scope="col" width="10%"> </th>
                        <td>
                            {receiveId}
                        </td>
                    </tr>
                    <tr>
                        <th scope="col" width="10%"> </th>
                        <td>
                            {senderId}
                        </td>
                    </tr>
                    <tr>
                        <th scope="col" width="10%"></th>
                        <td>
                            <input ref={titleRef} style={{width:800}} placeholder=" "></input>
                        </td>
                    </tr>
                    <tr>
                        <th scope="col" colSpan="2" width="10%"></th>
                    </tr>
                    <tr>
                        <td colSpan="2" className="view_text">
                            <textarea ref={contentRef} style={{width: 980, height: 300}} placeholder=" . (500)"></textarea>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
            {/*    */}
            <div align="center">
                <button  onClick={onClickReject} className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 100, color: "white"}}>
                    
                </button>
                <button onClick={onClickSubmit} className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 100, color: "white"}} >
                    
                </button>
            </div>
        </div>
    )

}

export default NoteWrite;